import { Tooltip } from '@material-ui/core'
import { Alarm, DirectionsCar, Store } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { Desktop, mediaDesktop, mediaMobile } from '../../../app/Responsive'
import { MarginBox } from '../../../atoms/MarginBox'
import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import {
  DeliveryTypeEnum,
  useMBranchDefaultTime,
  useQBranchDefeaultTimes,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { DeliveryTimeDialog } from '../../../molecules/DeliveryTimeDialog'
import { useOrderListModel } from '../hooks/useOrderListModel'

const StyledBranchDefaultTime = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  ${mediaDesktop`
    padding: 0.5rem 1rem;
  `};
  ${mediaMobile`
    padding: 1rem 0.5rem 0 0;
  `};
  .branch-default-time-item {
    margin: 0.4rem;
    display: flex;
  }
  .branch-default-time-item svg {
    margin: 0.2rem;
  }
`

export const BranchDefaultTime = observer(() => {
  const orderListModel = useOrderListModel()

  const branchId = orderListModel.firstBranchId

  const { data, loading } = useQBranchDefeaultTimes(
    {
      branchId: branchId!,
    },
    { skip: !branchId },
  )

  const [updateDeliveryTime] = useMBranchDefaultTime()

  if (!branchId) {
    return (
      <StyledBranchDefaultTime>
        <Tooltip title={i18n.t`Select a single branch to set delivery time`}>
          <TimeBadge variant={ETimeBadgeVariant.UNKNOWN}>
            <MarginBox margin="0.2rem 0 0 0">
              <Alarm />
            </MarginBox>
          </TimeBadge>
        </Tooltip>
      </StyledBranchDefaultTime>
    )
  }

  if (loading) {
    return null
  }

  const renderType = (
    deliveryType: DeliveryTypeEnum,
    value: number,
    title: string,
    icon: ReactNode,
  ) => (
    <DeliveryTimeDialog
      title={title}
      deliveryType={deliveryType}
      onConfirm={minutes => {
        updateDeliveryTime({
          branchId,
          deliveryType,
          time: minutes,
        })
      }}
    >
      {({ openDialog }) => (
        <div className="branch-default-time-item">
          <Desktop>{icon}</Desktop>
          <TimeBadge
            variant={ETimeBadgeVariant.NORMAL}
            minutes={value}
            onClick={openDialog}
          />
        </div>
      )}
    </DeliveryTimeDialog>
  )

  const { companyBranch } = data

  return (
    <StyledBranchDefaultTime>
      {renderType(
        DeliveryTypeEnum.Messenger,
        companyBranch.defaultDeliveryTime,
        i18n.t`Change default time of delivery to customer (in minutes)`,
        <DirectionsCar />,
      )}
      {renderType(
        DeliveryTypeEnum.Pickup,
        companyBranch.defaultPickupTime,
        i18n.t`Change default time of pickup (in minutes)`,
        <Store />,
      )}
    </StyledBranchDefaultTime>
  )
})
